.card {
    position: relative;
   
  
    transition: 0.3s;
   


    &:hover .infos {
        opacity: 1;
        transform: scaleY(1);
    }

    .infos {
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(105, 17, 206, 0.8);
        color: #fff;
        padding: 10px;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        opacity: 0;
        transform: scaleY(0);
        transition: 0.3s;
    }

   
}